.root {
    position: relative;
    height: 0;
    overflow: hidden;
    max-width: 100%;
}

.overlay {
    content: ' ';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    color: white;
    font-size: small;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    opacity: 1;
    -webkit-transition: opacity 2s ease-in-out;
    -o-transition: opacity 2s ease-in-out;
    transition: opacity 2s ease-in-out;
}

.overlay__cover {
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: cover;
}

.overlay__background {
    position: absolute;
    background-color: #111;
    -webkit-transition: background 2s ease-in-out;
    -o-transition: background 2s ease-in-out;
    transition: background 2s ease-in-out;
    width: 100%;
    height: 100%;
}

.root iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}


